import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import SpeedDialAction from "@mui/material/SpeedDialAction";

const dispatchedEvents = [];

const SrsMuiSpeedDialAction = function (props, ref) {
  return (
    <SpeedDialAction
      icon={props.icon}
      tooltipTitle={props.tooltipTitle}
      tooltipOpen={props.tooltipOpen}
      tooltipPlacement={props.tooltipPlacement}
      onClick={props.onClick}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiSpeedDialAction component */
`;

const props = [
  {
    name: "icon",
    defaultValue: null,
    type: "string",
  },
  {
    name: "tooltipTitle",
    defaultValue: null,
    type: "string",
  },
  {
    name: "tooltipOpen",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "tooltipPlacement",
    defaultValue: "left",
    type: "string",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiSpeedDialAction),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
